import "./App.css";
import WordFlicker from "./WordFlicker";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
  Button,
} from "@nextui-org/react";

const MainPage = () => {
  const redirectToLink = () => {
    window.open("https://kohaesthetics.janeapp.com", "_blank");
  };

  return (
    <>
      <div className="main-container">
        <header>
          <span className="logo">
            <a href="#" className="item">
              Koh Aesthetics
            </a>
            {/*<a href="#" className="item">
              About
            </a>*/}
            <Dropdown>
              <DropdownTrigger>
                <Button variant="bordered" className="item">
                  Open Menu
                </Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Static Actions">
                <DropdownItem key="1">Injectables</DropdownItem>
                <DropdownItem key="2">IV Infusion</DropdownItem>
                <DropdownItem key="3">PDO Threads</DropdownItem>
                <DropdownItem key="4">Weight Loss Program</DropdownItem>
                <DropdownItem key="5">Microneedling</DropdownItem>
                <DropdownItem key="6">Hair Restoration</DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </span>
        </header>
        <main>
          {/*<div className="background" src=""></div> */}

          <div
            className="app-button"
            style={{
              backgroundColor: "#C49DBC",
              color: "#FFFFFF",
              padding: "20px",
              textAlign: "center",
              width: "100%",
              height: "300px",
              zIndex: "11",
              justifyContent: "center",
              alignItems: "center",
              marginTop: "30px",
            }}
          >
            <h1
              style={{
                fontSize: "20px",
                marginBottom: "10px",
                color: "#FFFFFF",
              }}
            >
              Welcome to Koh Aesthetics!
            </h1>
            <p
              style={{
                fontSize: "15px",
                marginBottom: "20px",
                color: "#FFFFFF",
              }}
            >
              We're here to enhance your natural beauty with personalized care.
              Dr. Koh is dedicated to providing individualized aesthetic
              solutions that highlight your unique features. Interested in a
              personalized consultation? Book an appointment today!
            </p>
            <Button
              onClick={redirectToLink}
              style={{
                backgroundColor: "#FFFDF9",
                color: "#323232",
                fontWeight: "500",
                width: "100%",
              }}
            >
              Book an Appointment
            </Button>
          </div>
          <div className="why">
            <h1>Why Us?</h1>
            <p
              style={{
                fontSize: "15px",
                marginBottom: "20px",
                color: "#FFFFFF",
              }}
            >
              Choose Koh Aesthetics for a transformative med spa experience that
              seamlessly blends artistry with cutting-edge science. Founded by
              Dr. Koh, our commitment to your unique beauty sets us apart. With
              personalized treatments and a focus on the latest technologies, we
              deliver results that go beyond the ordinary. Trust us to bring out
              your timeless radiance - where expertise meets innovation
              effortlessly. Welcome to a world where your aesthetic aspirations
              become a stunning reality.
            </p>
          </div>
          <WordFlicker />

          <div class="header">
            <h1>Koh Aesthetics</h1>
          </div>

          <div class="bottom-banner">
            <p>Email: kohaestheticsnyc@gmail.com</p>
            <p>Phone: (646)771-1199</p>
            <p>Location: 501 5th Ave, #1203, Manhattan NY 10017</p>
            <p>Opening Hours: Mon-Fri 9am-6pm, Sat 10am-4pm</p>
          </div>
        </main>
      </div>
    </>
  );
};

export default MainPage;
